<script>
import ActBlock from '../components/Actblock'
import axios from 'axios'
export default {
    props: ['title', 'describe'],
    data() {
        return {
            eventlist: []
        }
    },
    created() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbzH3DIrMW5eBLzv-7Rxgq16ImUxZ5xd76QY4iM08cqZZlKOwdIqfP2JB6KTTo2uXu_q/exec',
                params: {
                    mode: 'all',
                    id: '950ac145-5aa9-4456-b48a-03e0b2da05ff'
                }
            })
            .then((response) => {
                this.eventlist = response.data;
                console.log(response.data)
            })
    },
    mounted() {

    },
    methods: {
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
    },
    components: {
        ActBlock
    }
}
</script>

