<template>
<div class="checksignupinfoblock">
    <div class="date">
        <span>{{convertdate(eventinfo[2])}}</span>
    </div>
    <div class="time">
        <span>{{eventinfo[3]}}</span>
    </div>
    <div class="signupinfo" v-for="(item,index) of signupinfolist" :key="index">
        <span class="signuptitle">{{item}}</span>
        <span class="answer">{{postsignupinfo[index]}}</span>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            msg: []
        }
    },
    methods: {
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
    },
    watch: {
        msg: function (newVal) { // plan1.0
            if (newVal) {
                console.log('监听到count')
                this.msg = this.$store.state.postsignupinfo
            } else {
                this.msg = '监听不到'
            }

        },
    },
    computed: {
        signupinfolist() {
            return this.$store.state.singuplist;
        },
        eventinfo() {
            return this.$store.state.signupinfo
        },
        postsignupinfo() {
            return this.$store.state.postsignupinfo
        }

    }
}
</script>

