<template>
<div class="usereventpage">
    <div class="dropdown">
        <ul @click="opendropdown">
            <div class="selecttag">
                {{currentstatus}}
                <i class="ri-arrow-drop-down-fill"></i>
            </div>
            <div class="typelist" v-show="menuopen">
                <li :class="{currenttag:status=='signup'}" @click.stop="clicked('signup')">已報名活動</li>
                <li :class="{currenttag:status=='history'}" @click.stop="clicked('history')">歷史紀錄</li>
            </div>

        </ul>
    </div>
    <div class="signupeventlist" v-show="status=='signup'">
        <h4 v-show="signupevent.length==0">
            尚未報名任何活動
        </h4>
        <eventblock :eventname="item.content[0]" :date="convertdate(item.content[2])" :time="item.content[3]" v-for="item of signupevent" :key="item.event"></eventblock>
    </div>
    <div class="historyeventlist" v-show="status=='history'">

        <eventblock :eventname="item.content[0]" :date="convertdate(item.content[2])" :time="item.content[3]" v-for="item of historylist" :key="item.event"></eventblock>
        <!-- <h4>{{historylist}}</h4> -->
    </div>
</div>
</template>

<script>
import eventblock from '../components/Profilepage-eventblock'
import axios from 'axios'
export default {
    data() {
        return {
            menuopen: false,
            status: 'signup',
            currentstatus: '已報名活動',
            alleventlist: [],
            filtersignuplist: []

        }
    },
    created() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbwu0IumvrBT9W5XybuCu-_hQxn8r8i37EQ-ziiR0N3Mfjkilx2ww_NRS8kEtqNMKV_I/exec',
                params: {
                    mode: 'all',
                    id: '950ac145-5aa9-4456-b48a-03e0b2da05ff'
                }
            })
            .then((response) => {
                this.alleventlist = response.data;
                console.log(this.alleventlist)

            })
    },
    mounted() {
        // // const n = 5;

    },
    methods: {
        opendropdown() {
            if (this.menuopen == false) {
                this.menuopen = true
            } else {
                this.menuopen = false
            }

        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
        clicked(val) {
            this.status = val;
            if (val == 'signup') {
                this.currentstatus = '已報名活動'
            } else {
                this.currentstatus = '歷史紀錄'
            }
            this.menuopen = false
        },
        multiFilter(array, filters) {
            const filterKeys = Object.keys(filters)
            // filters all elements passing the criteria
            return array.filter((item) => {
                // dynamically validate all filter criteria
                // console.log(item)
                return filterKeys.every(key => {
                    //ignore when the filter is empty Anne
                    if (!filters[key].length) return true
                    return !!~filters[key].indexOf(item[key])
                })
            })
        }

    },
    computed: {
        usereventinfo() {
            const oldl = this.$store.state.usereventinfo.length;
            let newusereventinfo = Array(oldl).fill().map(() => Array());
            for (let i = 0; i < this.$store.state.usereventinfo.length; i++) {
                for (let j = 0; j < this.$store.state.usereventinfo[i].length; j++) {
                    if (this.$store.state.usereventinfo[i][j] != '') {
                        newusereventinfo[i].push(this.$store.state.usereventinfo[i][j])
                    } else {
                        console.log('空白')

                    }

                }
            }
            return newusereventinfo
        },
        signupevent() {
            if (this.usereventinfo.length != 0) {
                //處理filter
                let eventfilters = {
                    eventid: []
                }

                //把原本的eventinfo結構改成新的物件結構
                let newsignupevent = []

                for (let i = 0; i < this.usereventinfo.length; i++) {
                    let eventidindex = this.usereventinfo[i].length - 2;
                    eventfilters.eventid.push(this.usereventinfo[i][eventidindex])

                }

                for (let i = 0; i < this.alleventlist.length; i++) {
                    let obj = {
                        eventid: '',
                        content: []
                    }
                    for (let j = 0; j < this.alleventlist[i].length; j++) {
                        //將除了id以外的內容塞到content當中
                        if (j == this.alleventlist[i].length - 1) {
                            obj.eventid = this.alleventlist[i][j]
                        } else {
                            obj.content.push(this.alleventlist[i][j])
                        }
                    }
                    newsignupevent.push(obj)

                }

                // eventfilters.eventid = new Set(eventfilters.eventid)
                // console.log('新的報名資料', newsignupevent)
                let filtersignuplist = this.multiFilter(newsignupevent, eventfilters);
                var today = new Date();
                let nowdays = new Date((today.getFullYear()) + '/' + (today.getMonth() + 1) + '/' + today.getDate());
                return filtersignuplist.filter((item) => {
                    let eventdate = new Date(this.convertdate(item.content[2]))
                    if (eventdate > nowdays) {
                        return item
                    }

                })
            } else {
                return []
            }

        },
        historylist() {
            if (this.usereventinfo.length != 0) {
                //處理filter
                let eventfilters = {
                    eventid: []
                }

                //把原本的eventinfo結構改成新的物件結構
                let newsignupevent = []

                for (let i = 0; i < this.usereventinfo.length; i++) {
                    let eventidindex = this.usereventinfo[i].length - 2;
                    eventfilters.eventid.push(this.usereventinfo[i][eventidindex])

                }

                for (let i = 0; i < this.alleventlist.length; i++) {
                    let obj = {
                        eventid: '',
                        content: []
                    }
                    for (let j = 0; j < this.alleventlist[i].length; j++) {
                        //將除了id以外的內容塞到content當中
                        if (j == this.alleventlist[i].length - 1) {
                            obj.eventid = this.alleventlist[i][j]
                        } else {
                            obj.content.push(this.alleventlist[i][j])
                        }
                    }
                    newsignupevent.push(obj)

                }

                // eventfilters.eventid = new Set(eventfilters.eventid)
                // console.log('新的報名資料', newsignupevent)
                let filtersignuplist = this.multiFilter(newsignupevent, eventfilters);
                var today = new Date();
                let nowdays = new Date((today.getFullYear()) + '/' + (today.getMonth() + 1) + '/' + today.getDate());
                return filtersignuplist.filter((item) => {
                    let eventdate = new Date(this.convertdate(item.content[2]))
                    if (eventdate < nowdays) {
                        return item
                    }

                })
            } else {
                return []
            }

        },
    },
    components: {
        eventblock
    }
}
</script>

