<template>
<div class="eventlistpage">
    <Headerbar></Headerbar>
    <br>
    <Searchbar @emitkeyword="searchkeyword" style="marginTop:70px" placehold="搜尋活動" width="328" height="43"></Searchbar>
    <div class="result">
        <span>以下為{{keyword}}搜尋結果：</span>
    </div>
    <div class="catawrap">
        <catatag :class="{tagclick:currenttagindex==index}" @typehandler="selecttype" v-for="(item,index) of taglist" :key="index" :tagname="item" :tagindex=index></catatag>
    </div>
    
    <div class="eventlist">
        <Actblockeventlist v-for="item of filtereventlist" :key="item[16]" :name="item[0]" :loc="item[4]" :date="convertdate(item[2])" :time="item[3]" :actin="item[7]" :waitingnum="item[8]" :type="item[6]" :id="item[16]" :pic="item[1]"></Actblockeventlist>
    </div>
    <Sidemenu v-show="showsidemenu"></Sidemenu>
</div>
</template>

<script>
import Headerbar from '../components/Header'
import Searchbar from '../components/Searchbar'
import catatag from '../components/catatag'
import Actblockeventlist from '../components/Actblock-eventlist'
import axios from 'axios'
import Sidemenu from '../components/Sidemenu'

export default {
    data() {
        return {
            taglist: ['親子', '手做', '戶外', '食物'],
            eventlist: [],
            keyword: this.$store.state.searchkeyword,
            currenttype: '',
            currenttagindex: '-1'
        }
    },
    components: {
        Headerbar,
        Searchbar,
        catatag,
        Actblockeventlist,
        Sidemenu
    },
    created() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbzH3DIrMW5eBLzv-7Rxgq16ImUxZ5xd76QY4iM08cqZZlKOwdIqfP2JB6KTTo2uXu_q/exec',
                params: {
                    mode: 'all',
                    id: '950ac145-5aa9-4456-b48a-03e0b2da05ff'
                }
            })
            .then((response) => {
                this.eventlist = response.data;
                console.log(response.data)
            })
    },
    methods: {
        searchkeyword(val) {
            this.keyword = val
            console.log(val)
        },
        selecttype(val, index) {

            if (this.currenttagindex == index) {
                this.currenttagindex = -1;
                this.currenttype = ''
            } else {
                this.currenttagindex = index
                this.currenttype = val
            }

        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
    },
    computed: {
        filtereventlist() {
            return this.eventlist.filter(item => {
                if (this.keyword == '' && this.currenttype == '') {
                    return this.eventlist
                } else {
                    //在種類是全部的時候，關鍵字搜尋
                    if (this.currenttype == '') {
                        if (item[0].search(this.keyword) != -1) {
                            console.log('種類全部、關鍵字搜尋')
                            return item
                        }
                        //在關鍵字為空（全部），種類篩選
                    } else if (item[0].search(this.keyword) == -1) {
                        if (this.currenttype == item[6]) {
                            console.log('關鍵字全部、種類搜尋')
                            return item
                        }

                    }
                    //在關鍵字篩選以及種類篩選的狀態下
                    else if (item[0].search(this.keyword) != -1 && this.currenttype == item[6]) {
                        console.log('關鍵字搜尋、種類搜尋')
                        return item
                    }

                }
                // else if(item[0].search(this.keyword) != -1){
                //     return item
                // }
                // else if(item[5]==this.currenttype&&item[0].search(this.keyword) != -1){
                //     return item
                // }
            })
        },
        showsidemenu() {
            return this.$store.state.showsidemenu
        },
    }
}
</script>

<style lang="scss" scoped>
.tagclick {
    background-color: #67d89e;
    border: none;
    color: white;
}

.eventlistpage {
    .catawrap {
        padding-left: 23px;
        box-sizing: border-box;
        width: 100%;
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        white-space: nowrap;
    }

    .result {
        padding-left: 23px;
        font-size: 18px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #111111;
        margin-bottom: 15px;
    }
}
</style>
