<template>
<div class="profilepage">
    <Headerbar></Headerbar>
    <div class="bgp"></div>
    <div class="userpic">

        <!-- <div class="pic"></div> -->
        <div class="pic" :style="{backgroundImage:'url('+userdata.pictureUrl+')'}"></div>

    </div>
    <div class="switchtab ">

        <div :class="{myact:true,current:this.$route.params.status == 'signupevent'}" @click="tomyact">
            <router-link :to="{
                name:'Profilepage',
                params:{status:'signupevent'}
            }">
                <i class="ri-coupon-2-fill"></i>
                <span>我的活動</span>
            </router-link>
        </div>

        <div :class="{profile:true,current:this.$route.params.status == 'profile'}" @click="toprofile">
            <router-link :to="{
                name:'Profilepage',
                params:{status:'profile'}
            }">
                <i class="ri-user-fill"></i>
                <span>個人資料</span>
            </router-link>
        </div>

    </div>
    <Profilecontent v-show="this.$route.params.status == 'profile'"></Profilecontent>
    <UserEventpage v-show="this.$route.params.status == 'signupevent'"></UserEventpage>
    <Sidemenu v-show="showsidemenu"></Sidemenu>
</div>
</template>

<script>
import Headerbar from '../components/Header'
import Profilecontent from '../components/Profilecontent'
import UserEventpage from '../components/UserEventpage'
import Sidemenu from '../components/Sidemenu'
export default {
    data() {
        return {
            currentstatus: 'profile',

        }
    },
    components: {
        Headerbar,
        Profilecontent,
        UserEventpage,
        Sidemenu
    },
    created() {
        this.$store.dispatch('getUserEventinfo')
    },
    mounted() {
        if (this.$route.params.status == 'signupevent') {
            this.currentstatus = 'myact'
        }
        if (this.$route.params.status == 'profile') {
            this.currentstatus = 'profile'
        }
    },
    methods: {
        tomyact() {
            this.currentstatus = 'myact'
        },
        toprofile() {
            this.currentstatus = 'profile'
        }
        // test() {
        //     this.$store.dispatch('sendmsg')
        // }
    },
    computed: {
        userdata() {
            return this.$store.state.userprofiledata;
        },
        showsidemenu() {
            return this.$store.state.showsidemenu
        },
    }
}
</script>

